.SearchBarDiv {
    display: flex;
    margin-left: 0px;
    justify-content: center;
    /* margin: auto; */
}

input {
    background-color: rgb(16, 19, 44);
    font-size: 18px;
    padding: 9.5px;
    border: none;
    border-radius: 0px;
    min-width: 0px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    padding-left: 17px;
    margin-top: 15px;
    margin-right: 0px;
}

.SearchButtonDiv {
    padding: 3.5px 5px 3.5px 0px;
    line-height: 0;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    background-color: rgb(16, 19, 44);
    margin-top: 15px;
    margin-left: 0px;
}

