.SearchButton {
    font-family: "Baloo 2";
    border: none;
    background-color: rgb(234, 104, 94);
    color: white;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
    transition: all 0.2s ease 0s;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    min-width: 40px;
    padding: 0px;
    border-radius: 50%;
}