.CategoryBarDiv {
    display: flex;
    flex-direction: row;
    max-width: 700px;
    justify-content: space-between;
    margin-left: 0px;
    -webkit-font-smoothing: antialiased;
    min-width: 0;
    box-sizing: border-box;
    overflow-x: scroll;
    margin: auto;
}
.CategoryBarDiv::-webkit-scrollbar-thumb {
    background-color: transparent;
}
.CategoryBarDiv:hover::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.5);
}

.CategoryButton {
    font-family: "Baloo 2";
    font-size: 15px;
    cursor: pointer;
    -webkit-box-align: center;
    align-items: center;
    background-color: rgb(16, 19, 44);
    border: 2px solid transparent;
    border-radius: 20px;
    color: rgb(228, 230, 235);
    display: flex;
    flex-shrink: 0;
    font-weight: bold;
    -webkit-box-pack: center;
    justify-content: center;
    margin-right: 7px;
    padding: 2px 7px;
    text-decoration: none;
    text-transform: capitalize;
    width: fit-content;
    min-width: 0;
    transition: all 0.2s linear;
}
.CategoryButton:hover {
    background-color: rgb(234, 104, 94);
}