@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&display=swap');

* {
  font-family: 'Baloo 2', 'cursive';
  color: white;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  /* background-color: #282c34; */
  background: rgb(29, 39, 72);
}

.App {
  max-width: 95%;
  /* overflow-x: scroll; */
  margin: auto;;
}

.AboutPersonDiv {
  border: 2px solid #61dafb;
  border-radius: 7px;
  width: fit-content;
  margin: auto;
  margin-top: 100px;
  padding: 10px;
}

.heartbeat-anim {
  animation: heartbeat normal 0.5s ease-in-out;
}

@keyframes heartbeat {
  0% {
    transform: scale(100%);
  }
  25% {
    transform: scale(90%);
  }
  75% {
    transform: scale(110%);
  }
  100% {
    transform: scale(100%);
  }
}

::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
}

#searchPageDiv {
  max-width: 100%;
  width: 85%;
  display: flex;
  margin: auto;
  margin-bottom: 30px;
}

.Item {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  flex-basis: 50%;
  /* max-width: 45%; */
  min-width: 30%;
  flex: 0 0 auto;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-bottom: 30px;
  border-radius: 20px;
  background-color: rgb(16, 19, 44);
  margin-right: 10px;
  padding-bottom: 20px;
}

/* CSS for Mobile Devices */
@media only screen and (max-width: 500px) {
  .ItemGridParent {
    flex-direction: row;
    flex-wrap: wrap;
    width: max-content;
  }

  #searchPageDiv {
    width: 100%;
  }

  .Item {
    max-width: 100%;
  }
}
